import React from "react";
import "../App.css";
import { Container } from "react-bootstrap";
import ProfileComment from "../com/ProfileComment";
import ProductCard from "../com/ProductCard";

class Product extends React.Component {
  render() {
    return (
      <Container style={{ minHeight: "100vh" }} fluid>
        <Container className="my-5 text-left">
          <ProductCard
            name="Awesome Product Name"
            price="121.81"
            text="Awesome product just try it is good buy it now"
            img="https://source.unsplash.com/random/680x400?text=Services2"
          />
          <ProfileComment />
        </Container>
      </Container>
    );
  }
}
export default Product;
