import React from "react";
import "../App.css";
import {
  Container,
  Row,
  Col,
  Table,
  Card,
  Alert,
  Spinner
} from "react-bootstrap";
import Axios from "axios";
class Transaction extends React.Component {
  state = {
    userid: JSON.parse(localStorage.getItem("user")).id,
    Transaction: [],
    TError: false,
    TEText: <Spinner animation="grow" size="lg" />,
    funds: <Spinner animation="grow" />
  };
  componentDidMount() {
    Axios.get(
      "https://mapay.ga/api/Transactions/" +
        JSON.parse(localStorage.getItem("user")).id,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }
    ).then(res => {
      this.setState({
        Transaction: res.data.data,
        TError: res.data.data.length > 0 ? true : false,
        TEText: res.data.data.length > 0 ? null : "There Is No Transactions"
      });
      console.log(res.data.data);
    });
    Axios.get(
      "https://mapay.ga/api/Wallets/" +
        JSON.parse(localStorage.getItem("user")).id,
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      }
    ).then(res => {
      console.log(res.data.data.funds);
      this.setState({ funds: res.data.data.funds });
    });
  }

  render() {
    const { funds } = this.state;
    return (
      <div className="App">
        <Container
          className="d-flex blue lighten-1 mb-5"
          style={{ minHeight: "20vh" }}
          fluid
        >
          <Container className="m-auto  text-center ">
            <h1>
              :) Hello Again, {JSON.parse(localStorage.getItem("user")).name}
            </h1>
          </Container>
        </Container>
        <Container className="my-5" style={{ minHeight: "100vh" }}>
          <Row>
            <Col md={3}>
              <Card>
                <Card.Header>Available Funds</Card.Header>
                <Card.Body>
                  <Card.Title>
                    <h1>${funds}</h1>
                  </Card.Title>
                </Card.Body>
              </Card>
            </Col>
            <Col md={9}>
              {this.state.TError ? (
                <Card>
                  <Table striped responsive>
                    <thead>
                      <tr>
                        <th>Date</th>
                        <th>Full Name</th>
                        <th>Amount</th>
                        <th>Verified</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.Transaction.map(tran => (
                        <tr key={tran.id}>
                          <td>{tran.time}</td>
                          <td>
                            {" "}
                            {this.state.userid !== tran.fromuserid
                              ? tran.fromname
                              : tran.toname}{" "}
                          </td>
                          <td>
                            {this.state.userid !== tran.fromuserid ? (
                              <i className="fas fa-plus success-ic" />
                            ) : (
                              <i className="fas fa-minus danger-ic" />
                            )}{" "}
                            ${tran.funds}
                          </td>
                          <td>
                            {tran.validatedby !== null ? (
                              <i className="fas fa-check success-ic" />
                            ) : (
                              <i className="fas fa-times danger-ic" />
                            )}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Card>
              ) : (
                <Alert variant="light">{this.state.TEText}</Alert>
              )}
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Transaction;
