import React from "react";
import "../App.css";
import { Container } from "react-bootstrap";
import ProfileCover from "../com/ProfileCover";
import ProfileProducts from "../com/ProfileProducts";
import ProfileComment from "../com/ProfileComment";
import { Redirect } from "react-router-dom";
import Axios from "axios";

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: null,
      Redirect: false
    };
  }

  componentDidMount() {
    if (this.props.match.params.id != null) {
      Axios.get(
        "https://mapay.ga/api/user/" + this.props.match.params.id,
        {
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
            Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
          }
        }
      )
        .then(res => {
          console.log(res.data);
          this.setState({ username: res.data.user.username });
        })
        .catch(error => {
          // handle error
          console.log(error);
          this.setState({ Redirect: true });
        });
    }
  }
  render() {
    return (
      <Container fluid>
        {this.state.Redirect ? <Redirect to="/NotFound" /> : null}
        <ProfileCover username={this.state.username} />
        <Container className="my-5 text-left">
          <ProfileProducts />
          <ProfileComment />
        </Container>
      </Container>
    );
  }
}
export default Profile;
