import React from "react";
import "../App.css";
import { Container, Row, Col, Spinner } from "react-bootstrap";
import Post from "../com/post";
import SideAd from "../com/ad";
import PostList from "../com/axios";
import Axios from "axios";

class Home extends React.Component {
  state = {
    post: [],
    loading: true,
    ad: [
      {
        name: "fullname",
        title: "Awsome text",
        text:
          "Alot of Asome text right here just dont read it because it will not give you any idea of the website you are just wasting your time reading this go do something good for you life instead of doing this!"
      },
      {
        name: "fullname",
        title: "Awsome text",
        text:
          "Alot of Asome text right here just dont read it because it will not give you any idea of the website you are just wasting your time reading this go do something good for you life instead of doing this!"
      },
      {
        name: "fullname",
        title: "Awsome text",
        text:
          "Alot of Asome text right here just dont read it because it will not give you any idea of the website you are just wasting your time reading this go do something good for you life instead of doing this!"
      }
    ]
  };

  componentDidMount() {
    Axios.get("https://mapay.ga/api/Posts", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
      }
    }).then(res => {
      this.setState({ post: res.data.data, loading: false });
    });
  }
  render() {
    const { post, ad } = this.state;
    console.log(post);
    return (
      <div className="App">
        <header className="App-header">
          <PostList />
          <Container className="my-5">
            <Row>
              <Col md={{ offset: 2 }}>
                {this.state.loading ? (
                  <Spinner animation="grow" size="lg" />
                ) : null}
                <Post item={post} />
              </Col>
              <Col sm={0} md={3}>
                <SideAd ad={ad} />
              </Col>
            </Row>
          </Container>
        </header>
      </div>
    );
  }
}

export default Home;
