import React from "react";
import {
  Card,
  Form,
  Col,
  Button,
  ListGroup,
  ListGroupItem,
  Image
} from "react-bootstrap";
import Textarea from "react-textarea-autosize";

function ProfileComment() {
  return (
    <Card>
      <Card.Body>
        <ListGroup className="list-group-flush">
          <ListGroupItem>
            <h5>
              <Image
                src="https://picsum.photos/40/40?text=hgdf"
                roundedCircle
              />
              {"  "}
              name
            </h5>
            comment
          </ListGroupItem>
        </ListGroup>
        <Form className="my-1">
          <Form.Row className="align-items-center">
            <Col>
              <Textarea
                className="form-control"
                style={{ resize: "none" }}
                placeholder="Comment"
              />
            </Col>
            <Col md={"auto"}>
              <Button className="btn-sm btn-rounded">Comment</Button>
            </Col>
          </Form.Row>
        </Form>
      </Card.Body>
    </Card>
  );
}

export default ProfileComment;
