import React from "react";
import { Container, Jumbotron, Image } from "react-bootstrap";

function ProfileCover(props) {
  return (
    <Container className="mb-5 text-left">
      <Jumbotron
        style={{
          height: 300,
          backgroundImage:
            'url("https://source.unsplash.com/random/800x300?text=' +
            props.username +
            '")',
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center center"
        }}
        fluid
      >
        <div style={{ position: "relative", bottom: -100, left: +20 }}>
          <Image
            style={{
              border: "5px solid",
              height: 180,
              width: 180,
              backgroundImage:
                'url("https://source.unsplash.com/random/180x180?text=' +
                props.username +
                '")',
              backgroundSize: "cover",
              backgroundRepeat: "no-repeat",
              backgroundPosition: "center center"
            }}
            className="border-light"
            roundedCircle
          />
          <h1 className="d-inline mx-5 text-light">
            {props.username != null
              ? props.username
              : JSON.parse(localStorage.getItem("user")).name}
          </h1>
        </div>
      </Jumbotron>
    </Container>
  );
}

export default ProfileCover;
