import React from "react";
import { Row, Col, Card } from "react-bootstrap";

function SideAd(props) {
  return props.ad.map((item, index) => (
    <Row key={index}>
      <Col>
        <Card className="m-1">
          <Card.Body>
            <Card.Title>{item.title}</Card.Title>
            <Card.Text>{item.text}</Card.Text>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  ));
}

export default SideAd;
